.wheel-menu {
    position: absolute;
    width: 200vh;
    height: 200vh;
    left: -45%;
    top: 50%;
    margin-top: -100vh;
    background-color: rgb(142, 142, 142);
    border-radius: 50%;
    border: 1vh solid white;
    outline: 15vh solid white;
    outline-offset: -15vh;
}

.main-title {
    display: flex;
    position: absolute;
    font-size: 7vh;
    width: 60vh;
    height: 60vh;
    left: 50%;
    top: 50%;
    margin-left: -30vh;
    margin-top: -30vh;
    color: black;
    text-align: center;
    align-items: center;
    justify-content: center;
}

.wheel-slot {
    display: flex;
    position: absolute;
    margin-left: -10vh;
    margin-top: -10vh;
    left: 50%;
    top: 50%;
    width: 20vh;
    height: 20vh;
    text-align: center;
    align-items: center;
    justify-content: center;
    font-family: 'consolas', 'Nanum Gothic';
}

.wheel-slot-label {
    display: flex;
    position: absolute;
    width: 20vh;
    height: 20vh;
    font-size: 10vh;
    text-align: center;
    align-items: center;
    justify-content: center;
}

.wheel-slot-title {
    display: flex;
    position: absolute;
    width: 100vh;
    height: 10vh;
    left: 30vh;
    font-size: 3vh;
    text-align: center;
    align-items: center;
    justify-content: left;
    color: darkgray;
}

.selected-label {
    font-size: 20vh;
}

.selected-title {
    font-size: 8vh;
    color: black;
}

.transitions-wrapper {
    position: relative;
    overflow: hidden;
    height: calc(var(--vh, 1vh) * 100);
    width: 100vw;
}

.right-enter {
    transform: translateX(100%);
}

.right-enter-active {
    transform: translateX(0);
    transition: transform 300ms ease-in-out;
}

.right-exit {
    transform: translateX(0);
}

.right-exit-active {
    transform: translateX(-100%);
    transition: transform 300ms ease-in-out;
}